.slick-dots {
	font-size: 0;
	@include xs {
		text-align: center;
		width: 175px; }
	li {
		display: inline-block;
		vertical-align: top;
		margin: 0 8px;
		width: 14px;
		height: 14px;
		background: $blue;
		border-radius: 50%;
		cursor: pointer;
		@include trans;
		@include m {
			margin: 10px; }
		&.slick-active {
			background: $orange4; }
		&:hover {
			@include mmin {
				background: $orange4; } }
		button {
			display: none; } } }

.section-advant {
