.reviews {
	// padding-bottom: 31px
	margin-bottom: 50px;
	// border-bottom: 1px solid $blue
	&__title {
		margin-bottom: 90px;
		// font-weight: 400
		@include m {
			margin-bottom: 45px; } }
	&__list {
		font-size: 0;
		min-height: 280px;
		margin-bottom: 40px;
		position: relative;
		@include m {
			min-height: 760px; }
		.review {
			margin-right: 2.75%;
			@include m {
				margin-right: auto; }
			&:nth-of-type(3n) {
				margin-right: 0;
				@include m {
					margin-right: auto; } } } }
	&__btn-block {
		text-align: center; }
	&__group {
		transition: all 0.6s;
		opacity: 0;
		transform-origin: 50% 50%;
		// transform: translateY(-200px)
		transform: scale(0);
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		@include m {
			transform: scale(1); }
		&.is-active {
			// transform: translateY(200px)
			transform: scale(1);
			opacity: 1;
			transition: all 0.8s;
			// animation-name: reviews
			// animation-duration: 0.6s
			// animation-delay: 0.2s
			@include m; }

		&.is-next-group {
			transform: translateY(200px); } } }

