@mixin rob {
	font-family: 'Roboto', sans-serif; }
@mixin lg1920 {
	@media only screen and (max-width: "5000px") {
		@content; } }
@mixin lg1700 {
	@media only screen and (max-width: "1700px") {
		@content; } }
@mixin lg1400 {
	@media only screen and (max-width: "1399px") {
		@content; } }
@mixin lg1300 {
	@media only screen and (max-width: "1299px") {
		@content; } }
@mixin lg {
	@media only screen and (max-width: "1169px") {
		@content; } }
@mixin lgmin {
	@media only screen and (min-width: "1170px") {
		@content; } }
@mixin l {
	@media only screen and (max-width: "1023px") {
		@content; } }
@mixin m {
	@media only screen and (max-width: "991px") {
		@content; } }
@mixin mmin {
	@media only screen and (min-width: "992px") {
		@content; } }
@mixin xs {
	@media only screen and (max-width: "767px") {
		@content; } }

$white: #fff;
$black: #000;
$black2: #000101;
$blue: #165673;
$blue-l: #0180ba;
$blue-bg: #4275b8;
$blue-bg2: #fafafa;
$blue-border: #1076bb;
$gray: #979797;
$gray2: #e8e8e8;
$gray-border: #165673;
$orange: #fe7f02;
$orange2: #86abc4;
$orange3: #ffef02;
$orange4: #febb01;
$orange5: #fcbe24;
$orange-bg: #fff3cb;
$orange-bg2: #feeca4;
$red: #fd140a;




a {
	text-decoration: none; }
h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }
body {
	background: $white;
	// +css-lock(16, 50, 600, 1000)
	line-height: 1.2;
	font-size: 17px;
	@include rob;
	font-weight: 300;
	@include smooth-font;
	color: $black;
	@include m {
		font-size: 14px; }
	&.is-hidden {
		overflow: hidden; } }
.out {
	overflow: hidden; }
.title-main {
	text-align: center;
	color: $blue;
	font-size: 36px;
	font-weight: 700;
	@include m {
		font-size: 20px;
		margin-bottom: 20px; }
	@include xs {
		font-size: 18px; } }
.title-lg {
	color: $white;
	font-size: 48px;
	font-weight: 700;
	@include m {
		font-size: 20px; }
	@include xs {
		font-size: 18px; } }
.title-m {
	color: $blue;
	font-weight: 400;
	margin-bottom: 37px;
	text-align: center;
	letter-spacing: 2.5px;
	@include m {
		letter-spacing: 1px;
		margin-bottom: 20px; }
	strong {
		font-weight: 700; }
	&--mb-big {
		margin-bottom: 45px;
		@include m {
			margin-bottom: 25px; } } }

.title-sm {
	color: $orange;
	font-size: 24px;
	font-weight: 500;
	@include m {
		font-size: 16px; } }

.section {
	padding-top: 56px;
	@include m {
		padding-top: 20px; }
	&--pt-big {
		padding-top: 65px;
		@include m {
			padding-top: 30px; } } }
