.header {
	padding-top: 17px;
	padding-bottom: 6px;
	border-bottom: 1px solid $gray-border;
	&__inner {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		@include m {
			display: block;
			@include clr; } }
	&__logo {
		display: block;
		margin-top: 3px;
		@include m {
			float: left;
			margin-bottom: 20px; }
		img {
			width: 251px;
			height: 51px;
			@include xs {
				width: 150px;
				height: auto; } } }
	&__center {
		text-align: center;
		@include m {
			float: right;
			margin-bottom: 20px; }
		@include xs {
			margin-top: 10px; }
		svg {
			fill: $orange;
			font-size: 26px;
			margin-right: 10px;
			display: inline-block;
			vertical-align: top;
			margin-top: 4px;
			@include xs {
				font-size: 16px;
				margin-top: 0;
				margin-right: 2px; } }
		a {
			color: $black2;
			font-size: 36px;
			font-weight: 700;
			display: block;
			margin-bottom: 1px;
			line-height: 1;
			@include xs {
				font-size: 16px; } }
		p {
			color: $blue;
			font-size: 17px;
			@include xs {
				display: none; } } }
	&__form {
		border: 1px solid $orange3;
		border-radius: 3px;
		padding: 3px;
		font-size: 0;
		margin-top: 3px;

		@include m {
			clear: both;
			max-width: 359px;
			margin-left: auto;
			margin-right: auto; }
		@include xs {
			border: none;
			max-width: 100%;
			text-align: center; }

		input {
			width: 190px;
			height: 42px;
			background-color: $white;
			border-left: 1px solid $gray;
			border-top: 1px solid $gray;
			border-bottom: 1px solid $gray;
			border-radius: 2px 0 0 2px;
			display: inline-block;
			vertical-align: top;
			line-height: 42px;
			padding: 0 6px;
			font-size: 17px;
			color: $black;
			@include placeholder {
				color: $gray; }
			@include xs {
				border: 1px solid $gray;
				border-radius: 2px;
				display: block;
				margin-left: auto;
				margin-right: auto;
				margin-bottom: 15px; } } } }


